<template>
	
  	<div class="lignes" :class="{ 'ok' : afficherLignes }">
  		<div class="lignes-gauche">
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  		</div>
  		<slot></slot>
  		<div class="lignes-droite">
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  			<div class="ligne"></div>
  		</div>
  	</div>


</template>


<script>
export default {
  name: 'Lignes',
  data () {
    return {
    	afficherLignes:false
    }
  },
  mounted() {
  	this.$bus.$on('lignes',(etat) => {
		  this.afficherLignes=etat
  	});

	setTimeout(() => {
  		this.$bus.$emit('lignes',true);
  	},2000);

  }
};

</script>

<style>
.lignes {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.lignes-gauche {
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
}

.lignes-droite {
	position: absolute;
	top: 0;
	right: 0;
	width: 50%;
	height: 100%;
}

.ligne {
	position: absolute;
	top: 0;
	height: 100%;
}
.lignes-gauche .ligne{
	left: -50%;
	transition: 0.5s ease;
}
.lignes-droite .ligne{
	right: -50%;
	transition: 0.5s ease;
}
.lignes.ok .lignes-gauche .ligne{
	left: 0;
}
.lignes.ok .lignes-droite .ligne{
	right: 0;
}
.lignes-gauche .ligne:nth-child(1){
	width:8%;
	background: #231f20;
	z-index: 4;
	transition: 0.2s ease;
}
.lignes-gauche .ligne:nth-child(2){
	width:12%;
	background: #ed1c24;
	z-index: 3;
	transition: 0.5s ease;
}

.lignes-gauche .ligne:nth-child(3){
	width:15%;
	background: #cededd;
	z-index: 2;
	transition: 0.7s ease;
}
.lignes-gauche .ligne:nth-child(4){
	width:25%;
	background: #c0924b;
	z-index: 1;
	transition: 1s ease;
}


.lignes-droite .ligne:nth-child(1){
	width:9%;
	background: #7b8990;
	z-index: 4;
	transition: 0.1s ease;
}
.lignes-droite .ligne:nth-child(2){
	width:12%;
	background: #231f20;
	z-index: 3;
	transition: 0.5s ease;
}

.lignes-droite .ligne:nth-child(3){
	width:16%;
	background: #c0924b;
	z-index: 2;
	transition: 0.7s ease;
}
.lignes-droite .ligne:nth-child(4){
	width:19%;
	background: #ed1c24;
	z-index: 1;
	transition: 1s ease;
}

</style>



