<template>
	
	<div>
		<div v-if="finJeu" :class="{bilanReady}">
			<div v-if="gagnant" class="bilan gagne">
				<center>
					<img class="texte" width="50%" src="../assets/bravo.png">
					<p v-show="lot">un <b>{{ lot.nom }}&nbsp;!</b></p>
					<div class="lot" :style="'background-image:url(/static/lots/'+lot.image+')'"></div>

				</center>
			</div>
			<div v-else class="bilan perdu">
			</div>
		</div>
		<div v-else class="compteur" :class="{boutonReady}">
			<div class="chiffre star"><template v-if="compteur<4">{{ compteur }}</template></div>
		</div>
	</div>

</template>


<script>
export default {
	name: 'Compteur',
	data () {
		return {
			finJeu:false,
			boutonReady:false,
			bilanReady:false,
			compteur:4,
			gagnant:false,
			lot:false
		}
	},
	mounted() {
		setTimeout(() => {
			this.boutonReady=true;
			this.start();
		},1000);
		
		this.$bus.$on('start',() => {
			this.start();
		});

	},
	methods : {
		start() {
			this.$bus.$emit('lignes',false);

			window.lancerCompteur = setInterval(() => {
				if(this.compteur<2) {
					clearInterval(window.lancerCompteur);
					this.end();
				} else {
					window.ion.sound.play('bip');
					this.compteur--;
				}
			},1000)	  			
		},
		end() {
			this.boutonReady=false;
			setTimeout(() => {
				this.$bus.$emit('lignes',true);
				this.finduJeu();

			},1000)

		},
		finduJeu() {
			this.finJeu=true;
			var r = Math.floor(Math.random()*2) + 1;
			this.gagnant = r==2;
			// this.gagnant=true;
			if(this.gagnant) {
				var random = Math.floor(Math.random() * this.$store.state.population.length)
				this.lot = this.$store.state.population[random]
				this.$store.commit('removeLot',random);

				window.ion.sound.play('tada');
				window.ion.sound.play('cheer');
			} else {
				window.ion.sound.play('buzz');
			}
			setTimeout(()=>{
				this.bilanReady=true;				
				this.$bus.$emit('fin');
			},500);

		}
	}
};

</script>

<style>
.compteur {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transform: scale(0);
	opacity: 0;
	transition: all 0.5s ease;
}
.boutonReady {
	transform: scale(1);
	opacity: 1;
}
.chiffre {
	position: absolute;
	top: 25%;
	left: 0;
	width: 100%;
	height: 50%;
	text-align: center;
	font-size: 23rem;
	color: white;
	text-shadow: 6px 6px black;
	font-weight: bold;
	z-index: 200;
	animation-name: warp;
	animation-duration: 500ms;
	animation-iteration-count: infinite;
	animation-timing-function: linear;	
}
.star:after {
	display: block;
	content:'';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: url(../assets/star.png) no-repeat center center;
	background-size: contain;
	z-index: -1;
	animation-name: spin;
	animation-duration: 1000ms;
	animation-iteration-count: infinite;
	animation-timing-function: linear;	
}

@keyframes spin {
	from {transform:rotate(0deg);}
	to {transform:rotate(360deg);}
}

@keyframes warp {
	0% {transform:scale(0.9);}
	50% {transform:scale(1);}
	100% {transform:scale(0.9);}
}

.bilan {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: 50% auto;
	background-repeat: no-repeat;
	background-position: center;	
	transform: scale(0);
	opacity: 0;
	transition:bottom 1s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 1s ease; 
}
.bilanReady .bilan {
	transform: scale(1);
	opacity: 1;
}
.gagne {
	font-size: 4rem;
}
.gagne .texte{
	margin-top:5%; 
	/*margin-bottom:2.5%; */
}
.gagne .lot{
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
    position: absolute;
    left: 35%;
	width: 30%;
	height: 30%;
	transform: scale(0);
	opacity: 0;
	transition:bottom 2s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 2s ease; 
}

.bilanReady .gagne .lot {
	transform: scale(1);
	opacity: 1;
}

.gagne p {
	color:black;
	max-width: 35%;
    line-height: 4rem;
    font-family: Quicksand, sans-serif;
	
}
	
.perdu {
	background-image: url(../assets/perdu.png)
}
</style>



