import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
	state : {
		lots : [],
		population: false
	},
	mutations : {
		setLots(state,data) {
			state.lots = [];
 			var population = [];
			data.lots.forEach(lot => {
				lot.probabilite = lot.qte/data.duree;
				state.lots.push(lot);
				for(var i=0;i<lot.qte;i++){
	 				population.push(lot);
				}
			});
			if(!state.population) {
				state.population = population;
			}
		},
		removeLot(state,index) {
			state.population.splice(index, 1);
		},
		initialiseStore(state) {
			if(localStorage.getItem(window.utils.store())) {
				var tmp = JSON.parse(localStorage.getItem(window.utils.store()));
				this.replaceState(Object.assign(state, tmp));
			}
		}
	},
})



