<template>
  <div class="footer" :class="{footerReady}">
  </div>
</template>

<script>
export default {
  name: 'Footer',
  data () {
    return {
    	footerReady:false
    }
  },
  mounted() {
  	setTimeout(() => {
  		this.footerReady=true
  	},500)  	
  }
};

</script>

<style>
.footer {
  position: fixed;
  bottom:-200px;
  width: 100%;
  left: 0;
  height: 100px;
  background: #c0924b url(../assets/a-gagner.png) no-repeat center center;
  background-size:auto 70%;
  opacity: 0;
  transition:bottom 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 0.5s ease; 
}
.footerReady {
	bottom: 0;
	opacity: 1;
}

</style>



