// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import utils from './libs/utils'
import axios from 'axios'
import store from './store'

window.axios = axios.create({
	baseURL: '/static/json/',
	params : { },
	headers: false
});


window.utils = utils;
window.prec=false;
Vue.config.productionTip = false

Vue.prototype.$bus = new Vue()

window.ion.sound({
	sounds: [
	{
		name: "bg",
		loop: true
	},
	{name: "bip"},
	{name: "tada"},
	{name: "buzz"},
	{name: "gong"},
	{name: "cheer"},
	{name: "tap"}
	],

    // main config
    path: "/static/sons/",
    preload: true,
    multiplay: true,
    volume: 0.2
});


store.subscribe((mutation, state) => {
	localStorage.setItem(window.utils.store(), JSON.stringify(state));
});


new Vue({
	el: '#app',
	store,
	components: { App },
	beforeCreate() {
		this.$store.commit('initialiseStore');
	},
	template: '<App/>'
})


