<template>
  <div class="header" :class="{headerReady}">
    <div class="header-left">
    </div>
    <div class="header-right">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      headerReady:false
    }
  },
  mounted() {
    setTimeout(() => {
      this.headerReady=true
    },250)    
  }
};

</script>

<style>

.header {
  position: fixed;
  top:-200px;
  width: 100%;
  left: 0;
  height: 100px;
  background: url(../assets/stripes.jpg) no-repeat center center;
  background-size:cover;
  transition:top 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 0.5s ease; 
}
.headerReady {
  top:0;
  opacity: 1;
}
.header-left {
  position: absolute;
  width: 40%;
  height: 100%;
  left: 0;
  top: 0;
  background: url(../assets/logo.png) no-repeat center center;
  background-size:auto 80%;
}
.header-right {
  position: absolute;
  width: 60%;
  height: 100%;
  right:0;
  top: 0;
  background: url(../assets/grand-openin.png) no-repeat center center;
  background-size:auto 50%;
}
</style>



