<template>
	<div class="body" :class="{animationBouton}">
		<Lignes>
			<Compteur v-if="buzzz"/>
			<div v-else class="bouton" ref="bouton"></div>
		</Lignes>
	</div>
</template>

<script>
import Lignes from './Lignes'
import Compteur from './Compteur'

export default {
	name: 'Body',
	components : { Lignes, Compteur },
	data () {
		return {
			animationBouton:false,
			buzzz:false,
			okFin:false
		}
	},
	mounted() {
		setTimeout(() => {
			this.animationBouton=true;
		},3000);

		window.onkeydown = (e) => {
			e = e || window.event;
			if(e.keyCode == 32) {
				// console.log(this.okFin)
				if(this.okFin) {
					document.location.href = document.location.href;
				}
				if(!this.buzzz) {
					window.ion.sound.play('gong');
					this.buzzz=true;
					this.$bus.$emit('start');
				}
			}
		};

		this.$bus.$on('fin',() => {
			this.okFin=true;
		})
	}
};

</script>

<style>
.body {
	position: fixed;
	top:100px;
	width: 100%;
	left: 0;
	height: calc(100% - 200px);
}
.bouton {
	position: absolute;
	top: 0;
	left: 15%;
	width: 70%;
	height: 100%;
	background: url(../assets/buzzez.png) no-repeat center center;
	background-size:contain;
	transition:transform 0.5s cubic-bezier(0.790, -0.285, 0.340, 1.335), opacity 0.5s ease; 
	transform: scale(0.1);
	opacity: 0;
	z-index: 100;
}
.animationBouton .bouton{
	transform: scale(1);
	opacity: 1;
}
</style>



