<template>
  <div class="app" ref="app">
    <template v-if="fullscreen">
      <Header/>
      <Body/>
      <Footer/>
    </template>
    <button v-else @click="goFullscreen" class="go-fullscreen">Cliquer ici pour commencer</button>
  </div>
</template>

<script>
import Header from './components/Header'
import Body from './components/Body'
import Footer from './components/Footer'

export default {
  name: 'App',
  data() {
    return {
      fullscreen:false
    }
  },
  components: {
    Header, Body, Footer
  },
  mounted() {
/*    window.onresize = () => {
        if(window.innerWidth == screen.width && window.innerHeight == screen.height) {
          this.fullscreen=true;
        } else {
          this.fullscreen=false;
        }
    }*/
    this.fullscreen=true;    

    window.axios.get('lots.json').then((response) => {
      this.$store.commit('setLots',response.data);
    })
  },
  methods : {
    goFullscreen() {
      utils.requestFullScreen(this.$refs.app);
    }
  }
};
</script>

<style>
html, body {
  overflow: hidden;
  height: 100%;
  width: 100%;
  cursor: none;
}
.app {
  background:white;
  position: fixed;
  top: 0;
  left: 0;  
  height: 100%;
  width: 100%;
}

.go-fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  color:white;
  text-align: center;
  z-index: 1000;
}
</style>



